import { TextField } from '@mui/material';

export function ClientWizard() {
  const { session } = useSession();
  const [activeStep, setActiveStep] = React.useState(0);
  const [basic, setBasic] = React.useState<Record<string, any>>({});
  const [radar, setRadar] = React.useState<RadarDraft>({});
  const isLast = activeStep === steps.length - 1;

  const handleNext = async () => {
    if (isLast) {
      const payload = {
        ...basic,
        radar,
        agencyEmail: session?.email,
      };
      await upsertClient(payload);
      setActiveStep(steps.length - 1);
      return;
    }
    setActiveStep((s) => s + 1);
  };
  const handleBack = () => setActiveStep((s)=> Math.max(0, s - 1));

  return (
    <Box>
      <Stepper activeStep={activeStep} alternativeLabel sx={{ mb: 3 }}>
        {steps.map((label) => (
          <Step key={label}>
            <StepLabel>{label}</StepLabel>
          </Step>
        ))}
      </Stepper>
      <Box sx={{ mb: 2 }}>
        {activeStep === 0 && (
          <ClientForm value={basic} onChange={setBasic} />
        )}
        {activeStep === 1 && (
          <PersonalInfoStep value={radar} onChange={(k,v)=>setRadar((p)=>({ ...p, [k]: v }))} />
        )}
        {activeStep === 2 && (
          <SocialStep value={radar} onChange={(k,v)=>setRadar((p)=>({ ...p, [k]: v }))} />
        )}
        {activeStep === 3 && (
          <ContentLinksStep value={radar} onChange={(k,v)=>setRadar((p)=>({ ...p, [k]: v }))} />
        )}
        {activeStep === 4 && (
          <EventsMetricsStep value={radar} onChange={(k,v)=>setRadar((p)=>({ ...p, [k]: v }))} />
        )}
        {activeStep === 5 && (
          <MotivationStep value={radar} onChange={(k,v)=>setRadar((p)=>({ ...p, [k]: v }))} />
        )}
        {activeStep === 6 && (
          <Box>
            <Typography variant="h6" gutterBottom>Review</Typography>
            <pre style={{ whiteSpace: 'pre-wrap' }}>{JSON.stringify({ ...basic, radar }, null, 2)}</pre>
          </Box>
        )}
      </Box>
      <Box sx={{ display: 'flex', justifyContent: 'space-between' }}>
        <Button disabled={activeStep === 0} onClick={handleBack}>Back</Button>
        <Button variant="contained" onClick={handleNext}>{isLast ? 'Create Client' : 'Next'}</Button>
      </Box>
    </Box>
  );
}


